@import url("https://cdn.syncfusion.com/ej2/material.css");

.search-field{
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px auto;
    width: 225px;
    box-shadow: 0 0 12px var(--border);
    height: 30px;
    border-radius: 10px;
    padding: 0 5px;
}

.search-field svg{
    margin-right: 5px;
}

.search-field input{
    border-radius: 10px;
    padding: 0 3px;
    background-color: var(--background);
    color: black;
    border: none;
    height: 80%;
    background: rgba(255,255,255,0);
}

.search-field input:focus{
    outline: none;
}

.search-field input::placeholder{
    color:black;
}

.clear-text{
    cursor: pointer;
}

.results{
    position: absolute;
    display: flex;
    flex-direction: column;
    left: 0;
    top: 30px;
    width: 100%;
    max-height: 217px;
    overflow-y: hidden;
    border-radius: 10px;
    border: 1px solid var(--border);
    background-color: var(--background);
    background: rgba(255,255,255,.4);
    z-index: 2;
}

.search-result{
    width: 100%;
    height: 60px;
    border-bottom: 1px solid var(--border);
    padding: 0 0 5px 5px;
    font-family: 'Poppins', sans-serif;
    font-size: 0.9rem;
    font-weight: 700;
    cursor: pointer;
    color: var(--text-primary);
}

.search-result:hover{
    background-color: #6770d214;
}

.search-result div:nth-of-type(1){
    padding-bottom: 5px;
}